



































	import uniMediaList from '@/components/uni-media-list/uni-media-list.vue';
	import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';

	import {
		friendlyDate
	} from '@/common/util.js';

	export default {
		components: {
			uniMediaList,
			uniLoadMore
		},
		data() {
			return {
				// 列表页
				pageStart:0,
				pageNum:5,
				loadingText: {
					contentdown: '上拉加载更多',
					contentrefresh: '正在加载...',
					contentnomore: '没有更多数据了'
				},
				scrollLeft: 0,
				refreshing: false,
				refreshText: '下拉可以刷新',
				videoList: [],
				tabIndex: 0,
				tabBars: [{
					name: '最新',
					video_type: 0,
					ref: 'new'
				}, {
					name: '新闻联播',
					video_type: 1,
					ref: 'company'
				}, {
					name: '校园生活',
					video_type: 2,
					ref: 'content'
				}, {
					name: '人物',
					video_type: 3,
					ref: 'xiaofei'
				}, {
					name: '文化',
					video_type: 4,
					ref: 'yule'
				}, {
					name: '理论',
					video_type: 5,
					ref: 'qukuailian'
				}, ]
			}
		},
		onLoad: function() {
			// 初始化列表信息
			this.tabBars.forEach((tabBar) => {
				this.videoList.push({
					data: [],
					requestParams: {
						columnId: tabBar.id,
						minId: 0,
						pageSize: 10,
						column: 'id,title,author,image,created,content'
					},
					loadingText: '加载中...'
				});
			});
			this.getList(0);
		},
		methods: {
			getList(type) {
				let that = this;
				console.log("进入getList ， type = "+type)
				//上拉加载更多
				if(type == 1){
					this.pageStart += 1;
				}else{//下拉刷新或第一次进入
					this.pageStart = 0;
				}
				console.log("==="+this.pageStart+"===")
				var data = {
					pageStart:this.pageStart,
					pageNum:this.pageNum,
					videoType:''
				};
				if(this.tabIndex != 0){
					data.videoType = this.tabIndex;
				}else{
					data.videoType='';
				}
				uni.request({
					url: this.websiteUrl+'uniApp/video/listVideo',
					data: data ,
					success: (result) => {
						console.log(result)
						this.refreshing = false;
						if( type == 1){
							that.videoList = that.videoList.concat(result.data.data.list);
						}else{//下拉刷新或第一次进入
							that.videoList = result.data.data.list;
							// console.log(that.videoList);
						}
					}
				});
			},
			goDetail(detail) {
				uni.navigateTo({
					url: '/pages/study/video/video-detial?detailDate=' + encodeURIComponent(JSON.stringify(detail))
				});
			},
			loadMore() {
				this.getList(1);
			},
			async changeTab(event) {
				let index = event.detail.current;
				if (this.isClickChange) {
					this.tabIndex = index;
					this.isClickChange = false;
					return;
				}
				let tabBar = await this.getElSize('tab-bar');
				let tabBarScrollLeft = tabBar.scrollLeft;
				let width = 0;

				for (let i = 0; i < index; i++) {
					let result = await this.getElSize(this.tabBars[i].ref);
					width += result.width;
				}
				let winWidth = uni.getSystemInfoSync().windowWidth,
					nowElement = await this.getElSize(this.tabBars[index].ref),
					nowWidth = nowElement.width;
				if (width + nowWidth - tabBarScrollLeft > winWidth) {
					this.scrollLeft = width + nowWidth - winWidth;
				}
				if (width < tabBarScrollLeft) {
					this.scrollLeft = width;
				}
				this.isClickChange = false;
				this.tabIndex = index;

				// 首次切换后加载数据
				const activeTab = this.videoList[this.tabIndex];
				if (activeTab.data.length === 0) {
					this.getList(0);
				}
			},
			getNodeSize(node) {
				return new Promise((resolve, reject) => {
					dom.getComponentRect(node, (result) => {
						resolve(result.size);
					});
				});
			},
			onRefresh(event) {
				this.refreshText = '正在刷新...';
				this.refreshing = true;
				this.getList(0);
			},
			getElSize(id) { //得到元素的size
				return new Promise((res, rej) => {
					uni.createSelectorQuery().select('#' + id).fields({
						size: true,
						scrollOffset: true
					}, (data) => {
						res(data);
					}).exec();
				});
			},
			async tapTab(index) { //点击tab-bar
				console.log(index)
				if (this.tabIndex === index) {
					return false;
				} else {
					let tabBar = await this.getElSize('tab-bar'),
						tabBarScrollLeft = tabBar.scrollLeft; //点击的时候记录并设置scrollLeft
					this.scrollLeft = tabBarScrollLeft;
					this.isClickChange = true;
					this.tabIndex = index;
					// 首次切换后加载数据
// 					const activeTab = this.videoList[this.tabIndex];
// 					if (activeTab.data.length === 0) {
						this.getList(0);
// 					}
					
				}
			},
		}
	}
